@media print {

  * {
    box-shadow: none !important;
    -webkit-transition: none !important;
    transition: none !important;
    transform: none !important;
  }

  html {
    font-size: 13px;
  }

  body {
    color: $c-font-heading !important;
  }

  .ma__stacked-row-section .main-content .page-content>* {
    margin-top: 0;
  }

  .ma__header__search,
  .ma__floating-action,
  .ma__content-eyebrow__tags,
  .ma__footer,
  .ma__header-alerts,
  #feedback,
  .messages--error,
  *[aria-hidden="true"],
  .ma__download-link__icon,
  .ma__page-banner__icon,
  .ma__page-banner__image,
  .ma__header__nav {
    display: none !important;
  }

  a::after {
    content: " (" attr(href) ") ";
    font-size: 0.8em;
    font-weight: normal;
  }

  .ma__page-banner {
    margin-bottom: 0;
    padding: 0;
    min-height: 0;

    &.ma__page-banner--overlay {
      height: auto;
    }

    .ma__page-banner__container {
      min-height: 0;
    }

    .ma__page-banner__content-wrapper,
    .ma__page-banner__title,
    .ma__page-banner__description {
      width: 100%;
      color: $c-font-heading;
    }

    .ma__page-banner__content {
      padding: 0;
      margin: 0;
      position: relative;
      max-width: none;
      width: 100%;
      height: auto;
      top: 0;
      max-width: none;
      color: $c-font-heading;
    }
  }

  .main-content {
    margin-bottom: 0;
  }

  .ma__contact-list  {
    break-inside: avoid-page;
  }

  .ma__callout-link  {
    border-width: 0 !important;
    break-inside: avoid;

    a {
      padding: 0;
    }
  }

  .ma__action-finder__header {
    margin-bottom: 0;
  }

  .ma__action-finder__container {
    padding-bottom: 0;
  }

  .ma__contact-us--accordion {
    border-width: 0;

    .ma__contact-us--accordion__status {
      display: none;
    }

    .ma__contact-us__content {
      display: block !important;
    }

    .ma__contact-group {
      width: 100%;
      max-width: 100%;
      border-width: 0;
    }
  }

  .ma__split-columns__column {
    padding: 0;
  }
}

